.cta {
  height: fit-content;
  width: fit-content;
  padding: 20px 50px;
  border-radius: 70%;
  text-align: center;
  border: 3px solid #1402ff;
  text-transform: uppercase;
  color: #1402ff;
  font-family: neue-haas-grotesk-text, sans-serif;
  font-weight: 700;
  font-style: normal;

  position: fixed;
  transform: rotate(20deg);
  bottom: 40px;
  left: 30px;
  transition: 0.3s;

  &:hover {
    transform: scale(1.7);
    bottom: 80px;
    left: 100px;
  }

  a {
    text-decoration: none;
  }
}

main .cta-contact {
  height: fit-content;
  width: fit-content;
  padding: 20px 50px;
  border-radius: 70%;
  text-align: center;
  border: 3px solid blue;
  text-transform: uppercase;
  color: blue;
  font-family: neue-haas-grotesk-text, sans-serif;
  font-weight: 700;
  font-style: normal;

  transform: rotate(20deg);
  bottom: 0px;
  left: 0px;
  transition: 0.3s;

  &:hover {
    transform: scale(1.2);
    bottom: 80px;
    left: 100px;
  }

  a {
    text-decoration: none;
  }

  p {
    margin: 10px;
    font-weight: 700;
    font-size: 0.8vw;
  }

  margin-left: 50px;
}

.marquee {
  position: relative;
  overflow: hidden;
  --offset: 30vw;
  --move-initial: calc(-25% + var(--offset));
  --move-final: calc(-50% + var(--offset));
  border-style: solid;
}

.heading-2 {
  font-weight: 400;
  font-size: 10vw;
  text-transform: uppercase;
  color: #1402ff;
  font-family: neue-haas-grotesk-text, sans-serif;
}

.heading-3 {
  font-weight: 400;
  font-size: 4vw;
  text-transform: uppercase;
  color: #1402ff;
  font-family: neue-haas-grotesk-text, sans-serif;
}

.marquee__inner {
  width: fit-content;
  display: flex;
  position: relative;
  transform: translate3d(var(--move-initial), 0, 0);
  animation: marquee 10s linear infinite;
  animation-play-state: paused;
}

.marquee span {
  padding: 0 4vw;
}

.marquee:hover .marquee__inner {
  animation-play-state: running;
}

@keyframes marquee {
  0% {
    transform: translate3d(var(--move-initial), 0, 0);
  }

  100% {
    transform: translate3d(var(--move-final), 0, 0);
  }
}

.logo:hover img {
  opacity: 0;
  animation-duration: 2s;
}

.logo:hover img {
  opacity: 0;
  transition: 1s;
  animation-name: spinningText;
}

